.ProjectItemDetail {
  font-size: 1.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

/* Display the project's title in the middle and put the Go Back
   to show the projects button next to it. */
.ProjectItemDetail-header {
  position: relative;
}

/* Go Back to show the projects button */
.ProjectItemDetail-header button {
  color: #5f5f5f;
  cursor: pointer;
  position: absolute;
  right: -35px;
  bottom: 1.7rem;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);

  background: none;
  border: none;
  font-size: 2rem;
}

.ProjectItemDetail-header button:hover {
  color: #0f0f0f;
}

/* Project's title */
.ProjectItemDetail .SectionHeader h3 {
  font-size: 2rem;
}

.ProjectItemDetail a {
  max-width: 100%;
  max-height: 100%;
}

.ProjectItemDetail img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;

  -webkit-transition: all .2s ease-in-out;

  -o-transition: all .2s ease-in-out;

  transition: all .2s ease-in-out;
}

.technologies {
  margin: 50px 0;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.technologies h3 {
  margin-bottom: 10px;
}

.technologies ul {
  padding-left: 60px;
}

.description h3 {
  margin-bottom: 10px;
}

.ProjectItemDetail-bottom {
  width: 100%;
  margin: 40px 0 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.ProjectItemDetail-bottom i {
  margin-right: 5px;
}

.project-link {
  margin-bottom: 20px;
  font-family: Arial;
  font-size: 1.5rem;
  text-decoration: none;

  border: none;
  background: none;
  color: #07f;
  cursor: pointer;
}

.project-link:hover {
  color: #0af;
}

@media only screen and (min-width: 767px) {
  .project-link {
    font-size: 1.75rem;
  }

  .ProjectItemDetail-bottom {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

@media only screen and (min-width: 1200px) {
  .ProjectItemDetail a {
    max-width: 60%;
    max-height: 60%;
  }

  .project-link {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 1600px) {
  .project-link {
    font-size: 2.25rem;
  }
}


