.Contact {
padding-top: 1px;
  padding-bottom: 100px;
}

.ContactForm button {
  font-family: Roboto;
  background: #2081D4;
  color: white;
  padding: 12px 36px;
  font-size: 2.2rem;
  border: 1px solid #005CAC;
  border-radius: 4px;
  cursor: pointer;
}

.ContactForm button:hover {
  background: #005CAC;
  margin-left: 2px;
}

.input-group {
  width: 100%;
  margin-bottom: 15px;
}

.input-group label {
  font-size: 1.4rem;
  line-height: 2.8rem;
}

.input-group input, textarea {
  width: 100%;
  padding: 16px 12px;
  font-size: 1.6rem !important;
  border-radius: 4px;
  border: 1px solid #9f9f9f;
  -webkit-transition: .1s all ease-in-out;
  -o-transition: .1s all ease-in-out;
  transition: .1s all ease-in-out;
}

.input-group textarea {
  margin-bottom: 10px;
}

.input-group input:focus, textarea:focus {
  border: 1px solid #039cf5;
  -webkit-box-shadow: 1px 0 1px #1D96FF, -1px 0 1px #1D96FF, 0 1px 1px #1D96FF, 0 -1px 1px #1D96FF;
          box-shadow: 1px 0 1px #1D96FF, -1px 0 1px #1D96FF, 0 1px 1px #1D96FF, 0 -1px 1px #1D96FF;
}


