.Home {
  width: 100%;
  height: 100vh;

  /* background: url("../../images/home.jpg") no-repeat center center fixed;  */
  background-size: cover;

  font-size: 1.4rem;
}

.info {
  position: absolute;
  top: 50%;
  left: 5%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);

  font-family: Montserrat;
  color: #fff;
  /* 1 bigger than darkish overlay in App.css */
  z-index: 2;  
}

.info h4 {
  font-weight: 600;
  line-height: 1.6rem;
}

.info h1 {
  line-height: 3.2rem;
}


