.Logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  font-size: 1.8rem;
  font-family: fantasy;
  
  position: relative;

  -webkit-transition: .22s all ease-in-out;

  -o-transition: .22s all ease-in-out;

  transition: .22s all ease-in-out;
}

.logo-img {
  max-width: 60px;
}

@media only screen and (min-width: 991px) {
  .Logo {
    left: 5%;
  }
}



